import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types'
import { graphql } from 'gatsby'

import { faExclamationTriangle } from '@fortawesome/free-solid-svg-icons'

import { AppContext } from '@src-components/contexts'
import { SEO, Layout } from '@src-components/foundations'
import { ArticleContent, PageHeading } from '@src-components/parts'

const Page = props => {
	const { location, data } = props
	const { body } = data.mdx

	const [isOpenMenu, setIsOpenMenu] = useState(false)
	const [isOpenSearch, setIsOpenSearch] = useState(false)
	const [isDarkMode, setIsDarkMode] = useState(null)

	const pageTitle = '404 - Page Not Found'
	const pageDescription =
		'アクセスしようとしたページが見つかりませんでした。削除されたか別のアドレスに変更された可能性があります。'

	const pageValues = {
		title: pageTitle,
		description: pageDescription,
		location: location.pathname,
		breadcrumbItems: [],
		isOpenMenu: isOpenMenu,
		setIsOpenMenu: setIsOpenMenu,
		isOpenSearch: isOpenSearch,
		setIsOpenSearch: setIsOpenSearch,
		isDarkMode: isDarkMode,
		setIsDarkMode: setIsDarkMode,
	}

	return (
		<AppContext.Provider value={pageValues}>
			<SEO />
			<Layout pattern="one-column">
				<PageHeading icon={faExclamationTriangle}>{pageTitle}</PageHeading>
				<ArticleContent>{body}</ArticleContent>
			</Layout>
		</AppContext.Provider>
	)
}

Page.propTypes = {
	location: PropTypes.object,
}

export default Page

export const pageQuery = graphql`
	{
		mdx(fields: { type: { eq: "basic" }, name: { eq: "page-not-found" } }) {
			id
			body
			frontmatter {
				title
				description
				thumbnail
			}
			fields {
				slug
				name
			}
		}
	}
`
